.faq-form {
  margin-top: 135px;
  padding-left: 20px;
}

.faq-form__wrapper {
  position: relative;
  min-height: 455px;
  background-image: url("../img/dawn-form-descktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-size: 845px 455px, 100%;
  background-position: top left;
  background-repeat: no-repeat;

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/dawn-form-descktop@2x.jpg");
  }
}

.faq-form__content {
  width: 56%;
  height: 315px;
  padding: 50px;
  box-sizing: border-box;
  background-color: @white;
  position: absolute;
  right: 0;
  top: 15%;
  box-shadow: 0px 4px 30px rgba(41, 54, 66, 0.1);
  border-radius: 5px;
  text-align: center;
}

.faq-form__title {
  .text-options (25px; @black; 32px; 600;);
  margin: 0;
}

.faq-form__sub-title {
  .text-options (16px; @black; 24px; normal;);
}

.faq-form__row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.faq-form__item {
  width: 48%;
}

.faq-form__input {
  width: 100%;
  .input-decoration();
}

.faq-form__button {
  width: 176px;
  padding-top: 18px;
  padding-bottom: 17px;
  .submit-decoration();
}

@media (max-width: @tablet-width) {
  .faq-form {
    margin-top: 125px;
    padding-left: 0;
  }

  .faq-form__content {
    min-height: 310px;
  }

  .faq-form__wrapper {
    min-height: 455px;
    background-image: url("../img/dawn-form-tablet@1x.jpg");
    .flexible-background();

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/dawn-form-tablet@2x.jpg");
    }
  }

  .faq-form__sub-title {
    margin-top: 12px;
    margin-bottom: 20px;
  }

  .faq-form__row {
    margin-bottom: 25px;
  }

  .faq-form__content {
    width: 85.6%;
    .vertical-position();
  }

  .faq-form__button {
    width: 250px;
    font-size: 10px;
    line-height: 12px;
  }
}

@media (max-width: @mobile-width) {
  .faq-form {
    margin-top: 50px;
    background-color: #FBF8F8;
  }

  .faq-form__wrapper {
    background: none;
    .set-padding (50px; 16px; 50px; 16px;);
    min-height: 440px;
  }

  .faq-form__content {
    background-color: #FBF8F8;
    position: relative;
    width: 100%;
    padding: 0;
    box-shadow: none;
    border-radius: 0%;
  }

  .faq-form__title {
    margin-bottom: 20px;
  }

  .faq-form__row {
    flex-direction: column;
    margin-bottom: 0;
  }

  .faq-form__item {
    width: 100%;
    margin-bottom: 20px;
  }

  .faq-form__button {
    width: 100%;
  }
}
