.title {
  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
}

.title--white {
  color: @white;
}

.title--dark {
  color: @semi-black;
}

.title--underlined-large {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: -35px;
    left: 0;
    width: 225px;
    height: 4px;
    background-color: @orange;
  }
}

.title--underlined-medium {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: -25px;
    left: 0;
    width: 175px;
    height: 4px;
    background-color: @orange;
  }
}

@media (max-width: @tablet-width) {
  .title {
    font-size: 30px;
    line-height: 45px;
  }

  .title--underlined-large,
  .title--underlined-medium {

    &::after {
      display: none;
    }
  }
}

@media (max-width: @mobile-width) {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
}
