.slider-single__item {
  display: flex !important; /* Этим свойством перекрываю свойство display:block slick слайдера */
  justify-content: space-between;
  padding-top: 80px;
}

.slider-single__location {
  width: 33%;
}

.slider-single__info {
  position: relative;
  width: 59.8%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-size: 430px 595px, 100%;
  background-position: top left;
  background-repeat: no-repeat;
  min-height: 595px;
}

.slider-single__title {
  margin-top: 15px;
  margin-bottom: 50px;
}

.slider-single__paragraph {
  line-height: 28px;
}

.slider-single__button {
  width: 250px;
  margin-top: 50px;
}

.slider-single__greece {
  background-image: url("../img/review-greece-desctop@1x.jpg");

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/review-greece-desctop@2x.jpg");
  }
}

.slider-single__albania {
  background-image: url("../img/review-albania-desctop@1x.jpg");

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/review-albania-desctop@2x.jpg");
  }
}

.slider-single__macedonia {
  background-image: url("../img/review-macedonia-desctop@1x.jpg");

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/review-macedonia-desctop@1x.jpg");
  }
}

.slider-single__montenegro {
  background-image: url("../img/review-montenegro-desctop@1x.jpg");

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/review-montenegro-desctop@2x.jpg");
  }
}

.slider-single__croatia {
  background-image: url("../img/review-croatia-desctop@1x.jpg");

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/review-croatia-desctop@2x.jpg");
  }
}

@media (max-width: @tablet-width) {
  .slider-single__item {
    flex-direction: column;
    padding-top: 15px;
  }

  .slider-single__title {
    margin-bottom: 20px;
  }

  .slider-single__paragraph {
    margin-bottom: 20px;
  }

  .slider-single__location {
    width: 100%;
    text-align: center;
    padding-right: 21px;
    padding-left: 21px;
    padding-bottom: 30px;
  }

  .slider-single__button {
    margin: 0 auto;
    padding-left: 0;
    text-align: center;
  }

  .slider-single__info {
    position: relative;
    width: 100%;
    min-height: 415px;
    .flexible-background();
  }

  .slider-single--greece {
    background-image: url("../img/review-greece-tablet@1x.jpg");

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/review-greece-tablet@2x.jpg");
    }
  }
}

@media (max-width: @mobile-width) {
  .slider-single__item {
    padding-top: 10px;
  }

  .slider-single__location {
    padding-right: 14px;
    padding-left: 13px;
  }

  .slider-single__info {
    margin-top: 20px;
  }

  .slider-single__button {
    width: 288px;
  }

  .slider-single--greece {
    background-image: url("../img/review-greece-mobile@1x.jpg");

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/review-greece-mobile@2x.jpg");
    }
  }
}
