.review {
  width: 382px;
  min-height: 367px;
  background: @white;
  .set-padding (0; 30px; 0; 30px;);
  box-sizing: border-box;
  position: absolute;
  right: 0;
  top: 19%;
}

.review__title {
  position: relative;
  .text-options (30px; @semi-black; 37px; 500;);

  &::after {
    content: "";
    position: absolute;
    bottom: -15px;
    left: 0;
    width: 68px;
    height: 0.73px;
    background-color: @orange;
  }
}

.review__paragraph {
  .text-options (14px; rgba(44, 46, 63, 0.7); 28px; normal;);
  margin-bottom: 25px;
}

.review__person {
  display: flex;
  align-items: center;
}

.review__logo {
  border-radius: 50%;
  margin-right: 15px;
}

.review__name {
  .text-options (16px; @semi-black; 24px; 500;);
}

@media (max-width: @tablet-width) {
  .review {
    .vertical-position();
  }

  .review__title {
    margin-bottom: 35px;
  }
}

@media (max-width: @mobile-width) {
  .review {
    width: 268px;
    background: @white;
    .set-padding (10px; 20px; 30px; 20px;);
    right: 5%;
    top: 12%;
  }

  .review__title {
    font-size: 20px;
    line-height: 24px;
    text-align: center;

    &::after {
      .vertical-position();
    }
  }

  .review__paragraph {
    text-align: center;
    margin-bottom: 15px;
  }

  .review__person {
    flex-direction: column;
  }

  .review__logo {
    margin-right: 0;
  }
}
