.page-footer {
  margin-top: 150px;
  margin-bottom: 75px;
}

.page-footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.page-footer__wrapper {
  display: flex;
  width: 66.6%;
}

.page-footer__column {
  width: 50%;
}

.page-footer__corporate {
  width: 33.3%;
}

.page-footer__title {
  margin-bottom: 20px;
  .text-options (18px; @black; 32px; 600;);
}

.page-footer__locations {
  padding-left: 15px;
}

.page-footer__locations-column {
  .reset-list();
  margin-right: 48px;
}

.page-footer__locations-column:last-child {
  margin-right: 0;
}

.page-footer__locations-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: -10px;
}

.page-footer__locations-link {
  display: block;
  padding-top: 10px;
  .text-options (14px; rgba(20, 21, 23, 0.5); 17px; normal;);
  text-decoration: none;
}

.page-footer__contacts {
  position: relative;
  padding-left: 100px;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 10px;
    width: 1px;
    height: 189px;
    background-color: #D7D9DF;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: -15px;
    width: 1px;
    height: 189px;
    background-color: #D7D9DF;
  }
}

.page-footer__email {
  position: relative;
  display: block;
  margin-bottom: 20px;
  padding-left: 45px;
  .text-options (14px; @orange; 17px; normal;);

  &::before {
    content: "";
    position: absolute;
    background: no-repeat 0 0 url(../img/icon-email.svg);
    top: 0px;
    left: 0px;
    width: 26px;
    height: 20px;
  }
}

.page-footer__phone {
  position: relative;
  display: block;
  padding-left: 45px;
  .text-options (16px; @orange; 32px; normal;);

  &::before {
    content: "";
    position: absolute;
    background: no-repeat 0 0 url(../img/icon-phone.svg);
    top: 0px;
    left: 10px;
    width: 30px;
    height: 30px;
  }
}

.page-footer__corporate {
  padding-left: 67px;
}

.page-footer__info {
  .text-options (14px; rgba(20, 21, 23, 0.5); 24px; normal;);
}

@media (max-width: @tablet-width) {
  .page-footer {
    margin-top: 60px;
    margin-bottom: 50px;
  }

  .page-footer__inner {
    padding-right: 21px;
    padding-left: 21px;
    flex-direction: column;
  }

  .page-footer__wrapper {
    width: 100%;
    padding-bottom: 30px;
    border-bottom: 1px solid #D7D9DF;
  }

  .page-footer__contacts {
    padding-left: 80px;

    &::before {
      left: 32px;
      width: 1px;
      height: 177px;
    }

    &::after {
      display: none;
    }
  }

  .page-footer__locations {
    padding-left: 0;
  }

  .page-footer__locations-column {
    margin-right: 48px;
  }

  .page-footer__corporate {
    width: 100%;
    text-align: center;
    padding-top: 10px;
    padding-left: 0;
  }
}

@media (max-width: @mobile-width) {
  .page-footer {
    margin-bottom: 25px;
  }

  .page-footer__inner {
    padding-right: 21px;
    padding-left: 11px;
  }

  .page-footer__wrapper {
    flex-direction: column;
    align-items: center;
    border: none;
  }

  .page-footer__column {
    width: 100%;
  }

  .page-footer__locations-list {
    justify-content: center;
    border-bottom: 1px solid #D7D9DF;
    padding-bottom: 10px;
  }

  .page-footer__title {
    margin-top: 10px;
  }

  .page-footer__contacts {
    padding-left: 0;
    padding-top: 20px;
    padding-bottom: 15px;
    border-bottom: 1px solid #D7D9DF;
    border-left: none;

    &::before {
      display: none;
    }
  }

  .page-footer__locations-column {
    width: 46%;
    margin-right: 20px;
    margin-bottom: 10px;
  }

  .page-footer__locations-column:nth-child(2n+2) {
    margin-right: 0;
  }

  .page-footer__locations,
  .page-footer__contacts {
    text-align: center;
  }

  .page-footer__phone::before {
    left: 45px;
  }

  .page-footer__email::before {
    left: 20px;
  }

  .page-footer__corporate {
    margin-top: -20px;
  }
}
