.button {
  display: block;
  background-color: @orange;
  .set-padding(30px; 0; 30px; 40px;);
  .text-options(12px; @white; 15px; bold;);
  letter-spacing: 2px;
  .reset-decoration();
  text-align: left;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;

  &:hover {

    cursor: pointer;
  }
}

.button:hover {
  background: @red;
}

.button--arrow {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    background: no-repeat 0 0 url(../img/icon-arrow.svg);
    top: 28px;
    right: 40px;
    width: 18px;
    height: 18px;
  }
}

@media (max-width: @tablet-width) {
  .button {
    .set-padding(19px; 0; 19px; 40px;);
    .text-options(10px; @white; 12px; 500;);
  }

  .button--arrow {

    &::after {
      background: no-repeat 0 0 url(../img/icon-arrow-tablet.svg);
      top: 18px;
      right: 40px;
    }
  }
}

@media (max-width: @mobile-width) {
  .button {
    .text-options(10px; @white; 12px; bold;);
  }
}
