.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.modal-wrapper {
  position: fixed;
  z-index: 777;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,.9);
}

.hidden {
  display: none;
}

.content-wrapper {
  margin: 0 auto;
  max-width: 1170px;
}

@media (max-width: @tablet-width) {
  .content-wrapper {
    max-width: 1023px;
  }
}

@media (max-width: @mobile-width) {
  .content-wrapper {
    max-width: 767px;
  }
}
