.buy-form {
  background-color: #FBF8F8;
  position: fixed;
  .centered-position();
  width: 728px;
  min-height: 444px;
  z-index: 888;
}

.buy-form__wrapper {
  width: 390px;
  min-height: 444px;
  background-color: #FBF8F8;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: auto;
  .set-padding (55px; 61px; 15px; 59px;);
}

.buy-form__container {
  background-image: url("../img/buy-tour-desctop@1x.jpg");
  .flexible-background();
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-size: 338px 444px, 100%;
  background-position: top left;
  background-repeat: no-repeat;
  min-height: 444px;

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/buy-tour-desctop@2x.jpg");
  }
}

.buy-form__title {
  .text-options (25px; @black; 43px; 600;);
  margin-bottom: 45px;
}

.buy-form__field {
  width: 100%;
}

.buy-form__input {
  width: 100%;
  margin-bottom: 20px;
  .input-decoration();
}

.buy-form__button {
  width: 176px;
  .submit-decoration();
}

.buy-form__close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  z-index: 5;

  &::after {
    transform: rotate(-45deg);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after,
  &::before {
    content: '';
    position: absolute;
    top: 8px;
    left: 0;
    width: 22px;
    height: 1px;
    background-color: rgba(20, 21, 23, 0.2);
  }
}

.buy-form--success,
.buy-form--error {
  .centered-decoration();
}

.buy-form__sucess-message {
  .text-options (25px; #48494D; 43px; 500;);
  text-align: center;
}

@media (max-width: @mobile-width) {
  .buy-form {
    width: 320px;
    min-height: 526px;
  }

  .buy-form__container {
    background: none;
  }

  .buy-form__wrapper {
    width: 100%;
    .set-padding (80px; 16px; 100px; 16px;);
  }

  .buy-form__title {
    margin-bottom: 50px;
  }

  .buy-form__button {
    width: 100%;
  }

  .buy-form__close {
    right: 20px;
    width: 22px;
  }
}
