.nav-list {
  .reset-list();
  width: 41.9%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: auto;
  flex-wrap: wrap;
}

.nav-list__item {
  position: relative;
}

.nav-list__item--about {
  margin-right: 8.1%;
}

.nav-list__item--prices {
  margin-right: 19.3%;
}

.nav-list__link {
  position: relative;
  display: block;
  .text-options(14px; @orange; 17px; 500;);
  text-decoration: none;
}

.nav-list__link--phone {
  font-size: 18px;
  line-height: 32px;
  font-weight: normal;

  &::before {
    content: "";
    position: absolute;
    background: no-repeat 0 0 url(../img/icon-phone.svg);
    top: 5px;
    left: -32px;
    width: 20px;
    height: 25px;
  }
}

@media (max-width: @semi-tablet-width) {
  .nav-list__link {
    font-size: 13px;
  }

  .nav-list__link--phone {
    font-size: 16px;
  }
}

@media (max-width: @tablet-width) {
  .nav-list {
    flex-direction: column;
    width: 100%;
  }

  .nav-list__item {
    width: 100%;
    margin: 0;
    background: #FBF8F8;
    .set-padding (15px; 0; 15px; 20px;);
    margin-bottom: 15px;
  }

  .nav-list__item:last-child {
    display: none;
  }

  .nav-list__link {
    .text-options (18px; #48494D; 24px; 500;);
    text-decoration: none;
  }

  .nav-list__link::before {
    content: "";
    position: absolute;
    background: no-repeat 0 0 url(../img/icon-right-mobile.svg);
    top: 5px;
    right: 100px;
    width: 13px;
    height: 13px;
  }
}

@media (max-width: @mobile-width) {
  .nav-list {
    padding-top: 85px;
  }

  .nav-list__item {
    .set-padding (15px; 0; 15px; 17px;);
    margin-bottom: 30px;
  }

  .nav-list__link {
    font-size: 20px;
    line-height: 28px;
    font-weight: 500;
  }

  .nav-list__link::before {
    right: 20px;
  }
}
