.locations__content {
  padding-top: 150px;
}

.locations__title {
  margin-bottom: 65px;
}

.locations__list {
  padding-top: 40px;
}

@media (max-width: @tablet-width) {
  .locations__content {
    .set-padding (55px; 81px; 75px; 81px;);
  }

  .locations__title {
    margin-bottom: 20px;
    text-align: center;
  }

  .locations__intro {
    text-align: center;
    line-height: 28px;
  }

  .locations__title br {
    display: none;
  }

  .locations__list {
    padding-top: 25px;
  }
}

@media (max-width: @mobile-width) {
  .locations__content {
    .set-padding (90px; 16px; 5px; 16px;);
  }

  .locations__intro br {
    display: none;
  }

  .locations__list {
    padding-top: 5px;
  }
}
