.button {
  display: block;
  background-color: #FE7865;
  padding-top: 30px;
  padding-right: 0;
  padding-bottom: 30px;
  padding-left: 40px;
  font-size: 12px;
  color: #ffffff;
  line-height: 15px;
  font-weight: bold;
  text-decoration: none;
  text-align: left;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.button:hover {
  cursor: pointer;
}
.button:hover {
  background: #F17D4B;
}
.button--arrow {
  position: relative;
}
.button--arrow::after {
  content: "";
  position: absolute;
  background: no-repeat 0 0 url(../img/icon-arrow.svg);
  top: 28px;
  right: 40px;
  width: 18px;
  height: 18px;
}
@media (max-width: 1023px) {
  .button {
    padding-top: 19px;
    padding-right: 0;
    padding-bottom: 19px;
    padding-left: 40px;
    font-size: 10px;
    color: #ffffff;
    line-height: 12px;
    font-weight: 500;
  }
  .button--arrow::after {
    background: no-repeat 0 0 url(../img/icon-arrow-tablet.svg);
    top: 18px;
    right: 40px;
  }
}
@media (max-width: 767px) {
  .button {
    font-size: 10px;
    color: #ffffff;
    line-height: 12px;
    font-weight: bold;
  }
}
.buy-form {
  background-color: #FBF8F8;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 728px;
  min-height: 444px;
  z-index: 888;
}
.buy-form__wrapper {
  width: 390px;
  min-height: 444px;
  background-color: #FBF8F8;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: auto;
  padding-top: 55px;
  padding-right: 61px;
  padding-bottom: 15px;
  padding-left: 59px;
}
.buy-form__container {
  background-image: url("../img/buy-tour-desctop@1x.jpg");
  background-size: cover;
  background-size: contain;
  background-position: center;
  background-size: 338px 444px, 100%;
  background-position: top left;
  background-repeat: no-repeat;
  min-height: 444px;
}
@media (min-resolution: 1.5dppx) {
  .buy-form__container {
    background-image: url("../img/buy-tour-desctop@2x.jpg");
  }
}
.buy-form__title {
  font-size: 25px;
  color: #141517;
  line-height: 43px;
  font-weight: 600;
  margin-bottom: 45px;
}
.buy-form__field {
  width: 100%;
}
.buy-form__input {
  width: 100%;
  margin-bottom: 20px;
  background: #ffffff;
  border: 1px solid rgba(44, 46, 63, 0.3);
  padding-top: 16px;
  padding-right: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  font-size: 14px;
  color: #141517;
  line-height: 17px;
  font-weight: normal;
}
.buy-form__button {
  width: 176px;
  display: block;
  margin: 0 auto;
  background-color: #FE7865;
  font-size: 12px;
  color: #ffffff;
  line-height: 15px;
  font-weight: bold;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding-top: 18px;
  padding-bottom: 17px;
  border: none;
  cursor: pointer;
}
.buy-form__close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  z-index: 5;
}
.buy-form__close::after {
  transform: rotate(-45deg);
}
.buy-form__close::before {
  transform: rotate(45deg);
}
.buy-form__close::after,
.buy-form__close::before {
  content: '';
  position: absolute;
  top: 8px;
  left: 0;
  width: 22px;
  height: 1px;
  background-color: rgba(20, 21, 23, 0.2);
}
.buy-form--success,
.buy-form--error {
  display: flex;
  align-items: center;
  justify-content: center;
}
.buy-form__sucess-message {
  font-size: 25px;
  color: #48494D;
  line-height: 43px;
  font-weight: 500;
  text-align: center;
}
@media (max-width: 767px) {
  .buy-form {
    width: 320px;
    min-height: 526px;
  }
  .buy-form__container {
    background: none;
  }
  .buy-form__wrapper {
    width: 100%;
    padding-top: 80px;
    padding-right: 16px;
    padding-bottom: 100px;
    padding-left: 16px;
  }
  .buy-form__title {
    margin-bottom: 50px;
  }
  .buy-form__button {
    width: 100%;
  }
  .buy-form__close {
    right: 20px;
    width: 22px;
  }
}
