.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide:focus {
  outline: none;
}
[dir='rtl'] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
@media (max-width: 1023px) {
  .slick-slider:first-child {
    width: 100%;
  }
}
.tariff {
  position: relative;
  padding-top: 10px;
  padding-right: 30px;
  padding-bottom: 30px;
  padding-left: 30px;
  background: #ffffff;
  border: 1px solid rgba(44, 46, 63, 0.3);
  box-sizing: border-box;
  border-radius: 6px;
}
.tariff__title {
  font-size: 28px;
  color: rgba(20, 21, 23, 0.7);
  line-height: 38px;
  font-weight: 600;
}
.tariff__price {
  font-size: 25px;
  color: rgba(20, 21, 23, 0.7);
  line-height: 34px;
  font-weight: bold;
}
.tariff__name-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.tariff__intro {
  position: relative;
  font-size: 16px;
  color: #2C2E3F;
  line-height: 24px;
  font-weight: normal;
  padding-right: 20px;
}
.tariff__intro::after {
  content: "";
  position: absolute;
  top: 75px;
  left: 0;
  width: 100%;
  height: 1px;
  border: 0.5px solid rgba(44, 46, 63, 0.3);
}
.tariff__advantages {
  padding: 0;
  margin: 0;
  list-style-type: none;
  padding-top: 45px;
  padding-left: 30px;
}
.tariff__advantage {
  position: relative;
  font-size: 16px;
  color: #2C2E3F;
  line-height: 32px;
  font-weight: normal;
  padding-bottom: 10px;
}
.tariff__advantage::before {
  content: "";
  position: absolute;
  background: no-repeat 0 0 url(../img/icon-checkbox.svg);
  top: 8px;
  left: -25px;
  width: 14px;
  height: 12px;
}
.tariff__button {
  width: 310px;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  padding-left: 0;
}
.tariff:hover {
  border: 3px solid #FE7865;
  cursor: pointer;
}
.tariff:hover .tariff__intro::after {
  border-color: #FE7865;
  opacity: 0.5;
}
.tariff:hover .tariff__advantage::before {
  background: no-repeat 0 0 url(../img/icon-arrow-orange.svg);
}
.tariff:hover .tariff__title {
  color: #FE7865;
}
.tariff:hover .tariff__price {
  color: #FE7865;
}
@media (max-width: 1023px) {
  .tariff {
    padding-top: 8px;
    padding-right: 20px;
    padding-bottom: 30px;
    padding-left: 20px;
  }
  .tariff__advantages {
    padding-left: 30px;
    padding-top: 15px;
  }
  .tariff__advantage {
    padding-bottom: 8px;
  }
  .tariff__advantage::before {
    left: -32px;
  }
  .tariff__name-wrapper {
    flex-direction: column;
    align-items: center;
  }
  .tariff__title {
    font-size: 24px;
    line-height: 34px;
    margin-bottom: -15px;
  }
  .tariff__intro {
    padding: 0;
    text-align: center;
    margin-top: 10px;
  }
  .tariff__intro::after {
    top: 80px;
  }
  .tariff__price {
    margin-bottom: 0;
  }
  .tariff__button {
    width: 100.6%;
    bottom: -2px;
  }
}
.tariff-price__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 230px;
}
.tariff-price__content {
  display: flex;
  width: 65.8%;
}
.tariff-price__info {
  width: 25%;
  padding-top: 65px;
}
.tariff-price__title {
  margin-bottom: 55px;
}
.tariff-price__offer {
  width: 48%;
  min-height: 672px;
}
.tariff-price__offer--complex {
  margin-right: 50px;
}
.tariff-price__contacts {
  margin-top: 60px;
  margin-bottom: 30px;
  padding-left: 50px;
}
.tariff-price__action {
  display: block;
  font-size: 22px;
  color: #141517;
  line-height: 34px;
  font-weight: 500;
  margin-bottom: 10px;
}
.tariff-price__phone {
  position: relative;
  font-size: 20px;
  color: #000000;
  line-height: 32px;
  font-weight: 500;
  text-decoration: none;
}
.tariff-price__phone::before {
  content: "";
  position: absolute;
  background: no-repeat 0 0 url(../img/icon-phone-orange.svg);
  top: 0px;
  left: -45px;
  width: 30px;
  height: 30px;
}
@media (max-width: 1023px) {
  .tariff-price__wrapper {
    padding-right: 21px;
    padding-left: 21px;
    flex-direction: column;
    margin-top: 8px;
  }
  .tariff-price__title {
    margin-bottom: 20px;
  }
  .tariff-price__contacts {
    margin-top: 35px;
    padding-left: 0;
  }
  .tariff-price__action {
    display: block;
    font-size: 18px;
    margin-bottom: 10px;
  }
  .tariff-price__phone {
    font-size: 18px;
    color: #FE7865;
    text-decoration: underline;
  }
  .tariff-price__phone::before {
    display: none;
  }
  .tariff-price__info {
    width: 100%;
    text-align: center;
    order: -1;
  }
  .tariff-price__content {
    width: 100%;
    padding-right: 50px;
    padding-left: 50px;
  }
  .tariff-price__offer {
    width: 46%;
    min-height: 629px;
  }
}
@media (max-width: 767px) {
  .tariff-price__wrapper {
    padding-right: 15px;
    padding-left: 17px;
  }
  .tariff-price__offer--complex {
    min-height: 610px;
  }
  .tariff-price__offer--economic {
    min-height: 630px;
  }
  .tariff-price__content {
    padding: 0;
  }
  .tariff-price__contacts {
    margin-top: 20px;
    margin-bottom: 45px;
  }
  .tariff-price__action {
    font-size: 18px;
    line-height: 34px;
  }
  .tariff-price__offer--complex {
    margin-right: 0;
  }
  .tariff-price__phone {
    font-size: 18px;
    color: #FE7865;
    line-height: 32px;
    font-weight: 500;
    text-decoration: underline;
  }
  .tariff-price__content {
    flex-direction: column;
  }
  .tariff-price__offer {
    width: 100%;
  }
  .tariff-price__offer:first-child {
    margin-bottom: 30px;
  }
}
.title {
  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
}
.title--white {
  color: #ffffff;
}
.title--dark {
  color: #2C2E3F;
}
.title--underlined-large {
  position: relative;
}
.title--underlined-large::after {
  content: "";
  position: absolute;
  bottom: -35px;
  left: 0;
  width: 225px;
  height: 4px;
  background-color: #FE7865;
}
.title--underlined-medium {
  position: relative;
}
.title--underlined-medium::after {
  content: "";
  position: absolute;
  bottom: -25px;
  left: 0;
  width: 175px;
  height: 4px;
  background-color: #FE7865;
}
@media (max-width: 1023px) {
  .title {
    font-size: 30px;
    line-height: 45px;
  }
  .title--underlined-large::after,
  .title--underlined-medium::after {
    display: none;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
}
body {
  min-width: 320px;
  margin: 0 auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  line-height: 24px;
  color: #2C2E3F;
  background-color: #ffffff;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.modal-wrapper {
  position: fixed;
  z-index: 777;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.9);
}
.hidden {
  display: none;
}
.content-wrapper {
  margin: 0 auto;
  max-width: 1170px;
}
@media (max-width: 1023px) {
  .content-wrapper {
    max-width: 1023px;
  }
}
@media (max-width: 767px) {
  .content-wrapper {
    max-width: 767px;
  }
}
