.main-nav {
  display: flex;
  padding-top: 35px;
  padding-bottom: 33px;
}

.main-nav__toggle {
  display: none;
}

@media (max-width: @tablet-width) {
  .main-nav {
    flex-direction: column;
    padding: 0;
  }

  .main-nav__logo-wrapper {
    display: flex;
    align-items: center;
    min-height: 50px;
  }

  .main-nav__toggle {
    display: block;
    position: relative;
    margin-left: auto;
    padding: 0;
    border: 0;
    width: 72px;
    height: 50px;
    color: @grey;
    background-color: @orange;
  }

  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 17px;
    right: 25px;
    width: 22px;
    height: 1px;
    background-color: @grey;
    box-shadow: 0 8px 0 0 @grey, 0 16px 0 0 @grey;
  }

  .main-nav--opened .main-nav__toggle {
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 22px;
      right: 23px;
      width: 25px;
      height: 1px;
      background-color: @grey;
    }

    &::before {
      transform: rotate(45deg);
      box-shadow: none;
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__list {
    display: block;
  }
}

@media (max-width: @mobile-width) {
  .main-nav__toggle {
    width: 50px;
    height: 50px;
  }

  .main-nav--closed .main-nav__toggle::before {
    top: 16px;
    right: 15px;
  }

  .main-nav--opened .main-nav__toggle {
    &::before,
    &::after {
      top: 24px;
      right: 13px;
    }
  }
}
