.tariff {
  position: relative;
  .set-padding (10px; 30px; 30px; 30px;);
  background: @white;
  border: 1px solid rgba(44, 46, 63, 0.3);
  box-sizing: border-box;
  border-radius: 6px;
}

.tariff__title {
  .text-options (28px; rgba(20, 21, 23, 0.7); 38px; 600;);
}

.tariff__price {
  .text-options (25px; rgba(20, 21, 23, 0.7); 34px; bold;);
}

.tariff__name-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.tariff__intro {
  position: relative;
  .text-options (16px; @semi-black; 24px; normal;);
  padding-right: 20px;

  &::after {
    content: "";
    position: absolute;
    top: 75px;
    left: 0;
    width: 100%;
    height: 1px;
    border: 0.5px solid rgba(44, 46, 63, 0.3);
  }
}

.tariff__advantages {
  .reset-list();
  padding-top: 45px;
  padding-left: 30px;
}

.tariff__advantage {
  position: relative;
  .text-options (16px; @semi-black; 32px; normal;);
  padding-bottom: 10px;

  &::before {
    content: "";
    position: absolute;
    background: no-repeat 0 0 url(../img/icon-checkbox.svg);
    top: 8px;
    left: -25px;
    width: 14px;
    height: 12px;
  }
}

.tariff__button {
  width: 310px;
  position: absolute;
  bottom: 5%;
  .vertical-position();
  text-align: center;
  padding-left: 0;
}

.tariff:hover {
  border: 3px solid #FE7865;
  cursor: pointer;
}

.tariff:hover .tariff__intro::after {
  border-color: @orange;
  opacity: 0.5;
}

.tariff:hover .tariff__advantage::before {
  background: no-repeat 0 0 url(../img/icon-arrow-orange.svg);
}

.tariff:hover .tariff__title {
  color: @orange;
}

.tariff:hover .tariff__price {
  color: @orange;
}

@media (max-width: @tablet-width) {
  .tariff {
    .set-padding (8px; 20px; 30px; 20px;);
  }

  .tariff__advantages {
    padding-left: 30px;
    padding-top: 15px;
  }

  .tariff__advantage {
    padding-bottom: 8px;

    &::before {
      left: -32px;
    }
  }

  .tariff__name-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .tariff__title {
    font-size: 24px;
    line-height: 34px;
    margin-bottom: -15px;
  }

  .tariff__intro {
    padding: 0;
    text-align: center;
    margin-top: 10px;

    &::after {
      top: 80px;
    }
  }

  .tariff__price {
    margin-bottom: 0;
  }

  .tariff__button {
    width: 100.6%;
    bottom: -2px;
  }
}
