.reset-list() {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.reset-decoration() {
  text-decoration: none;
}

.centered-decoration() {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flexible-background() {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.vertical-position() {
  left: 50%;
  transform: translateX(-50%);
}

.centered-position() {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.input-decoration() {
  background: @white;
  border: 1px solid rgba(44, 46, 63, 0.3);
  .set-padding (16px; 15px; 15px; 15px;);
  .text-options (14px; @black; 17px; normal;);
}

.submit-decoration() {
  display: block;
  margin: 0 auto;
  background-color: @orange;
  .text-options (12px; @white; 15px; bold;);
  letter-spacing: 2px;
  text-transform: uppercase;
  padding-top: 18px;
  padding-bottom: 17px;
  border: none;
  cursor: pointer;
}

.text-options (@font-size; @color; @line-height; @font-weight;) {
  font-size: @font-size;
  color: @color;
  line-height: @line-height;
  font-weight: @font-weight;
}

.set-padding (@padding-top; @padding-right; @padding-bottom; @padding-left;) {
  padding-top: @padding-top;
  padding-right: @padding-right;
  padding-bottom: @padding-bottom;
  padding-left: @padding-left;
}
