.promo-text__intro {
  margin-bottom: 35px;
}

.promo-text__button {
  width: 340px;
}

@media (min-width: @desktop-width-only) {
  .promo-text__wrapper {
    position: relative;
    min-height: 400px;
  }

  .promo-text__content {
    width: 32%;
    position: absolute;
    top: 5px;
    right: 95px;
  }
}

@media (max-width: @tablet-width) {
  .promo-text__wrapper {
    width: 100%;
    padding-top: 0;
    padding-right: 170px;
    padding-left: 170px;
  }

  .promo-text__intro {
    margin-bottom: 10px;
    text-align: center;
  }

  .promo-text__button {
    width: 250px;
    margin: 0 auto;
  }

}

@media (max-width: @mobile-width) {
  .promo-text__wrapper {
    padding-top: 5px;
    padding-right: 16px;
    padding-left: 16px;
  }

  .promo-text__intro {
    margin-bottom: 20px;
    color: #2C2E3F;
  }

  .promo-text__button {
    width: 288px;
  }
}
