.list {
  .reset-list();
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.list__item {
  position: relative;
  width: 29.9%;
  margin-right: 5.1%;
  margin-bottom: 100px;
  min-height: 430px;
  .flexible-background();
}


.list__item:nth-child(3n+3) {
  margin-right: 0;
}

.list__title {
  position: absolute;
  bottom: 0;
  .vertical-position();
  width: 61.4%;
  background-color: @white;
  padding-top: 17px;
  padding-bottom: 17px;
  .text-options(24px; @semi-black; 29px; 500;);
  text-align: center;
  margin: 0;
}

.list__actions {
  display: none;
}

.list__actions {
  position: absolute;
  .centered-position();
  width: 76%;
  padding-top: 30px;
  padding-bottom: 30px;
  border: 1px solid rgba(255, 255, 255, 0.7);
}

.list__item:hover .list__actions {
  display: block;
}

.list__info {
  display: block;
  .text-options(14px; @white; 17px; 500;);
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  letter-spacing: 2px;
}

.list__item:hover > .list__overlay {
  width: 100%;
  height: 100%;
  background: rgba(20, 21, 23, 0.6);
}

.list__item--greece {
  background-image: url("../img/cat-greece-desctop@1x.jpg");

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/cat-greece-desctop@2x.jpg");
  }
}

