    width: 47.5%;
    margin-right: 4.9%;
    margin-bottom: 25px;
    min-height: 425px;
  }

  .list__item:nth-child(2n+2) {
    margin-right: 0;
  }

  .list__item:nth-child(3n+3) {
    margin-right: 3.7%;
  }

  .list__item:last-child {
    margin-right: 0;
  }

