.promo {
  position: relative;
  background-image: url("../img/header-image-desktop@1x.jpg");
  .flexible-background();
  background-color: #527EA5;
  min-height: 860px;

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/header-image-desctop@2x.jpg");
  }
}

.promo__wrapper {
  background: none;
}

.promo__title {
  margin-top: 0;
}

@media (min-width: @desktop-width-only) {
  .promo__content {
    position: relative;
    min-height: 350px;
  }

  .promo__title {
    width: 35.5%;
    position: absolute;
    top: 185px;
    right: 55px;
  }
}

@media (max-width: @tablet-width) {
  .promo {
    background-image: url("../img/header-image-tablet@1x.jpg");
    min-height: 344px;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/header-image-tablet@2x.jpg");
    }
  }

  .promo__title {
    width: 100%;
    padding-top: 60px;
    margin-bottom: 15px;
    text-align: center;
  }
}

@media (max-width: @mobile-width) {
  .promo {
    background: none;
  }

  .promo__wrapper {
    .centered-decoration();
    background-image: url("../img/header-image-tablet@1x.jpg");
    .flexible-background();
    background-color: #527EA5;
    min-height: 235px;

    @media (min-resolution: @retina-dppx) {
      background-image: url("../img/header-image-mobile@2x.jpg");
    }
  }

  .promo__title {
    padding-top: 18px;
    padding-right: 16px;
    padding-left: 16px;
  }
}
