.tariff-price__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 230px;
}

.tariff-price__content {
  display: flex;
  width: 65.8%;
}

.tariff-price__info {
  width: 25%;
  padding-top: 65px;
}

.tariff-price__title {
  margin-bottom: 55px;
}

.tariff-price__offer {
  width: 48%;
  min-height: 672px;
}

.tariff-price__offer--complex {
  margin-right: 50px;
}

.tariff-price__contacts {
  margin-top: 60px;
  margin-bottom: 30px;
  padding-left: 50px;
}

.tariff-price__action {
  display: block;
  .text-options (22px; @black; 34px; 500;);
  margin-bottom: 10px;
}

.tariff-price__phone {
  position: relative;
  .text-options (20px; #000000; 32px; 500;);
  .reset-decoration();

  &::before {
    content: "";
    position: absolute;
    background: no-repeat 0 0 url(../img/icon-phone-orange.svg);
    top: 0px;
    left: -45px;
    width: 30px;
    height: 30px;
  }
}

@media (max-width: @tablet-width) {
  .tariff-price__wrapper {
    padding-right: 21px;
    padding-left: 21px;
    flex-direction: column;
    margin-top: 8px;
  }

  .tariff-price__title {
    margin-bottom: 20px;
  }

  .tariff-price__contacts {
    margin-top: 35px;
    padding-left: 0;
  }

  .tariff-price__action {
    display: block;
    font-size: 18px;
    margin-bottom: 10px;
  }

  .tariff-price__phone {
    font-size: 18px;
    color: @orange;
    text-decoration: underline;

    &::before {
      display: none;
    }
  }

  .tariff-price__info {
    width: 100%;
    text-align: center;
    order: -1;
  }

  .tariff-price__content {
    width: 100%;
    padding-right: 50px;
    padding-left: 50px;
  }

  .tariff-price__offer {
    width: 46%;
    min-height: 629px;
  }
}

@media (max-width: @mobile-width) {
  .tariff-price__wrapper {
    padding-right: 15px;
    padding-left: 17px;
  }

  .tariff-price__offer--complex {
    min-height: 610px;
  }

  .tariff-price__offer--economic {
    min-height: 630px;
  }

  .tariff-price__content {
    padding: 0;
  }

  .tariff-price__contacts {
    margin-top: 20px;
    margin-bottom: 45px;
  }

  .tariff-price__action {
    font-size: 18px;
    line-height: 34px;
  }

  .tariff-price__offer--complex {
    margin-right: 0;
  }

  .tariff-price__phone {
    .text-options (18px; @orange; 32px; 500;);
    text-decoration: underline;
  }

  .tariff-price__content {
    flex-direction: column;
  }

  .tariff-price__offer {
    width: 100%;
  }

  .tariff-price__offer:first-child {
    margin-bottom: 30px;
  }
}
